import React from 'react'

import {
  Home,
  About,
  Work,
} from './views'

import {
  SplineTest
} from './blog/SplineTest'

import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

export const Routes = () => {

  return(
  <Router>
  <Switch>

  <Route exact path="/">
    <Home />
  </Route>

  <Route path='/about'>
  <About />
  </Route>

  <Route path='/blog'>
  <SplineTest />
  </Route>

  </Switch>
  </Router>
);
}
